@font-face {
  font-family: '2Lv_med';
  src: url(../font/NEXON_Lv2_Gothic_Medium.ttf);
}
@font-face {
  font-family: '2Lv_bold';
  src: url(../font/NEXON_Lv2_Gothic_Bold.ttf);
}
@font-face {
  font-family: '2Lv_l';
  src: url(../font/NEXON_Lv2_Gothic_Light.ttf);
}
@font-face {
  font-family: 'kcc';
  src: url(../font/KCC-Murukmuruk.ttf);
}

*{
  margin: 0;
  padding: 0;
}

ul,li,ol {
  list-style: none;
}

body {
  background-color: #333;
  position: relative;
}

#root {
  display: flex;
  flex-direction: row;
}

#Main_Nav {
  width: 7%;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  position: relative;

  .Main_Nav_wrap{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    color: #999;
    background-color: #333;
    border-right: 2px dashed #999;
    width: 139px;
    height: 100%;
    position: fixed;

    .Nav_Bar {
    width: 100%;
    height: 300px;

    .Nav_bar_Menu {
      width: 100%;
      height: 100%;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      align-items: center;

      .Nav_Home {
        transform: rotate(-90deg);
      }
      .Nav_Work {
        transform: rotate(-90deg);
      }
      .Nav_About {
        transform: rotate(-90deg);
      }
      .Nav_Contact {
        margin-top: 50px;
        transform: rotate(-90deg);
      }
      span {
        cursor: pointer;
        font-family: '2Lv_med';

        &:hover {
          text-decoration: underline;
          color: tomato;
        }
      }
    }
  }

  .Active {
    color: tomato;
  }

  .Main_Nav_Line{
    width: 1px;
    height: 200px;
    background-color: #999;
  }

  .footer {
    width: 100%;
    height: 300px;

    p {
      font-size: 18px;
      transform: rotate(-90deg) translateX(-200px);
    }
  }
  }


}

#Home_Wrap {
  width: 93%;
  height: 100vh;
  display: flex;
  flex-direction: row;

  .Home_info {
    width: 30%;
    height: 100%;
    display: flex;
    flex-direction: column-reverse;
    align-items: center;

    .Home_info_box {
      width: 80%;
      height: 30%;
      margin-bottom: 100px;
      margin-left: 60px;

      .info_box_title{
        height: 30px;
        font-size: 20px;
        display: flex;
        flex-direction: row;
        align-items: flex-end;
        margin-bottom: 5px;
        font-family: '2Lv_bold';

        h3 {
          padding-left: 20px;
          color: #fff;
          font-size: 20px;
        }
        h4 {
          color: #ccc;
          margin-left: 10px;
        }
      }
      .info_box_body {
        padding-left: 20px;
        color: #fff;

        .box_body_add {
          line-height: 25px;
        }
        
        h3 {
          margin-bottom: 3px;
        }

        .box_body_line {
          width: 95%;
          height: 1px;
          background-color: #fff;
          margin-top: 10px;
          margin-bottom: 10px;
        }

        .box_body_memo {
          color: #ccc;
          line-height: 25px;

          h4 {
            font-family: '2Lv_l';
            margin-bottom: 5px;
          }
          span {
            font-family: '2Lv_l';
            &:hover {
              cursor: pointer;
              text-decoration: underline;
            }
          }
        }
      }
    }
  }
  .Home_menu {
    width: 70%;
    height: 100%;
    display: flex;
    flex-direction: row;
    justify-content: center;

    .Home_menu_wrap {
      width: 80%;
      height: 100%;
      display: flex;
      flex-direction: column;
      justify-content: space-around;
      align-items: center;
      margin-left: 60px;

      ul{
        font-family: 'kcc';
        li{
          span {
            font-size: 150px;
            margin-bottom: 20px;
            color: #fff;
          }
          .ul_Home {
            color: tomato;
            cursor: pointer;
          }
          .ul_hover {
            &:hover{
              color: tomato;
              cursor: pointer;
              font-style: italic;
            }
          }
        }
      }
    }
  }
}

#Work_wrap {
  height: 100vh;
  display: flex;
  position: absolute;
  top: 0;
  left: 7%;

  .Work_info {
    width: 30%;
    height: 100%;
    display: flex;
    flex-direction: column-reverse;
    align-items: center;

    .Work_info_box {
      width: 80%;
      height: 30%;
      margin-bottom: 100px;
      margin-left: 60px;
      font-family: '2Lv_bold';

      .Work_info_title {
        color: #eee;
        font-size: 20px;
        margin-bottom: 15px;
      }
      .Work_info_add {
        p {
          font-size: 18px;
          color: #eee;
          font-family: '2Lv_l';
          line-height: 25px;
        }
        .Work_line_box {
          width: 95%;
          height: 1px;
          background-color: #fff;
          margin-top: 10px;
          margin-bottom: 10px;
        }
        .Work_add_node_box {
          h4 {
            font-family: '2Lv_l';
            margin-bottom: 5px;
            color: #eee;
            line-height: 25px;
            font-weight: lighter;
            span{
              font-family: '2Lv_l';
              &:hover {
              cursor: pointer;
              text-decoration: underline;
              }
            }
          }
        }
      }
    }
  }
  .Work_menu {
    width: 70%;
    height: 100%;
    display: flex;
    flex-direction: row;
    justify-content: center;
    .Work_menu_wrap {
      width: 80%;
      height: 100%;
      display: flex;
      flex-direction: column;
      justify-content: space-around;
      align-items: center;
      margin-left: 60px;
      ul{
        font-family: 'kcc';
        li{
          margin-bottom: 30px;
          span {
            font-size: 100px;
            color: #fff;

            b {
              font-size: 32px;
              color: #666;
              font-weight: 100;
            }
          }
          .Work_hover {
            &:hover{
              font-style: italic;
              color: tomato;
              cursor: pointer;
            }
          }
        }
      }
    }
  }
}

#modal_first {
  width: 25%;
  height: 45%;
  position: absolute;
  top: 50px;
  left: 250px;
  background-color: #fff;
  color: #333;
  font-family: '2Lv_med';

  .modal_first_title {
    padding: 10px;
    h3 {
      font-size: 20px;
      margin-bottom: 10px;
    }
    img {
        width: 100%;
        height: 300px;
      }
  }
  .modal_first_data {
    padding-left: 12px;
    margin-bottom: 5px;
  }
  .modal_first_line {
    width: 95%;
    height: 1px;
    background-color: #333;
    margin: 0 auto;
  }
  .modal_first_skill {
    padding-left: 12px;
    margin-top: 10px;
    span {
      padding: 5px 10px;
      background-color: #444;
      border-radius: 5px;
      color: #eee;
      margin-right: 5px;
    }
  }
}

