      display: flex;
      flex-direction: column;
      justify-content: space-around;
      align-items: center;
      margin-left: 60px;
      font-family: 'kcc';

      ul {
        li {
          margin-bottom: 20px;
          span {
            font-size: 60px;
            margin-bottom: 20px;
            color: #fff;

            b {
              font-size: 40px;
              color: #666;
            }
          }
        }
