#About_wrap {
  width: 93%;
  height: 100vh;
  display: flex;
  flex-direction: row;

  .About_Left {
    width: 50%;
    height: 100%;
    position: relative;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: flex-start;

    .About_Img {
      width: 40%;
      height: 40%;
      border: 1px solid #fff;
      margin-top: 100px;
      display: flex;
      justify-content: center;
      align-items: center;
      color: #fff;
      position: relative;
      z-index: 3;

      img {
        width: 100%;
        height: 100%;
      }
    }
    .About_Img_shadow {
      width: 40%;
      height: 40%;
      border: 1px solid #aaa;
      position: absolute;
      top: 120px;
      left: 235px;
      background-color: #aaa;
      z-index: 1;
    }
  }

  .About_Right {
    width: 50%;
    height: 100%;

    .About_Info {
      width: 80%;
      height: 100%;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: flex-start;
      
      .About_Info_Title {
        color: #ddd;
        margin-bottom: 30px;
        font-family: '2LV-b';
      }
      .About_Info_Add {
        color: #eee;
        line-height: 25px;
        font-family: '2Lv_l';
      }
      .About_Info_intro {
        color: #eee;
        margin-top: 30px;
        display: flex;
        flex-flow: row wrap;
        font-family: '2Lv_l';
        
        h3 {
          width: 50%;
          margin-top: 10px;
          font-weight: normal;
        }

        .About_Skill {
          width: 100%;
          height: 300px;
          display: flex;
          flex-flow: row wrap;
          justify-content: center;
          align-items: center;
          margin-top: 20px;
          background-color: #bbb;
          border-radius: 30px;

          img {
            width: 80px;
            height: 80px;
            margin-right: 10px;
            margin-top: 10px;
          }
        }
      }
    }
  }
}

#Node_modal_box {
  width: 1000px;
  height: 600px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50%);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: #eee;

  .node_modal_box_head {
    width: 100%;
    height: 5%;
    background-color: #999;
    span {
      float: right;
      margin-right: 10px;
      font-size: 22px;
      color: #fff;
      cursor: pointer;
      font-weight: bold;
    }
  }
  .node_modal_box_body {
    width: 95%;
    height: 95%;
    video {
      width: 100%;
      height: 100%;
    }
  }
}

.motion_box {
  width: 100%;
}

